import { PlayerAnimation } from './playerAnimation';

const NUMBER_DOWN_FRAMES = 4;
const NUMBER_SIDE_FRAMES = 4;
const NUMBER_UP_FRAMES = 4;

export class GoblinMeleeAttackAnimation extends PlayerAnimation {

    public constructor(username: string, position: PIXI.Point, gridDimension: PIXI.Point) {
        const textures = GoblinMeleeAttackAnimation.loadTextures('enemy', 'melee', NUMBER_DOWN_FRAMES,
                                                                NUMBER_SIDE_FRAMES, NUMBER_UP_FRAMES);
        super(textures.up, textures.down, textures.side, username, position, gridDimension);
        this.actionName = 'melee';
        this.animation.onComplete = () => {
            this.endAnimation();
        };
    }
}



