import { PlayerAnimation } from './playerAnimation';
import { Texture } from 'pixi.js';

export abstract class NoDirectionPlayerAnimation extends PlayerAnimation {

    protected static loadTextures(color: string, actionName: string, nDownFrames: number, nSideFrames: number, nUpFrames: number ): any {
        const downTextures = [PIXI.Texture.fromFrame(`walk_down_${color}_1.png`).clone()] as Array<Texture>;
         for (let i = 1; i <= nDownFrames; i++) {
            downTextures.push(PIXI.Texture.fromFrame(`${actionName}_${color}_${i}.png`).clone());
        }
        return {
            down: downTextures ,
            side: [],
            up: []
        };
    }

    public updateTexture(newBaseTexture: PIXI.BaseTexture): void {
        this.downTextures.forEach(texture => {
            texture.baseTexture = newBaseTexture;
        });
    }
}



