import * as PIXI from 'pixi.js';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { WebSocketService } from '../shared/providers/web-socket.service';
import { filter, map } from 'rxjs/operators';
import { IMessage } from '../shared/messages/iMessage';
import { MessageType } from '../shared/messages/messageType';
import { SyncMessage } from '../shared/messages/syncMessage';
import { SpriteSheetMessage } from '../shared/messages/spriteSheetMessage';
import { UpdateViewMessage } from '../shared/messages/updateViewMessage';
import { TurnMessage } from '../shared/messages/turnMessage';

@Injectable()
export class GameService {

    private readonly spritesheetData$: Observable<SpriteSheetMessage>;
    private readonly mapData$: Observable<SyncMessage>;
    private readonly gameTurn$: Observable<TurnMessage>;

    public constructor(private webSocketService: WebSocketService) {
        this.spritesheetData$ = this.webSocketService.message$.pipe(
            filter((message: IMessage) => {
                return message.type === MessageType.Spritesheet;
            }),
            map((message: SpriteSheetMessage) => {
                return message;
            })
        );

        this.mapData$ = this.webSocketService.message$.pipe(
            filter((message: IMessage) => {
                return message.type === MessageType.Sync;
            }),
            map((message: SyncMessage) => {
                return message;
            })
        );

        this.gameTurn$ = this.webSocketService.message$.pipe(
            filter((message: IMessage) => {
                return message.type === MessageType.Turn;
            }),
            map((message: TurnMessage) => {
                return message;
            })
        );
    }

    public getSpritesheets(): Observable<SpriteSheetMessage> {
        return this.spritesheetData$;
    }

    public getMapInfo(): Observable<SyncMessage> {
        return this.mapData$;
    }

    public getTurn(): Observable<TurnMessage> {
        return this.gameTurn$;
    }

    public requestNewView(mapSize: PIXI.Rectangle, position: PIXI.Point): void {
        this.webSocketService.send(new UpdateViewMessage(mapSize, position));
    }
}



