import { NoDirectionPlayerAnimation } from './noDirectionPlayerAnimation';

const NUMBER_DOWN_FRAMES = 10;
const NUMBER_SIDE_FRAMES = 0;
const NUMBER_UP_FRAMES = 0;

export class HealingAnimation extends NoDirectionPlayerAnimation {

    public constructor(username: string, position: PIXI.Point, gridDimension: PIXI.Point, color: string) {
        const textures = HealingAnimation.loadTextures(color, 'potion', NUMBER_DOWN_FRAMES, NUMBER_SIDE_FRAMES, NUMBER_UP_FRAMES);
        super(textures.up, textures.down, textures.side, username, position, gridDimension);
        this.actionName = 'potion';
        this.animation.onComplete = () => {
            this.endAnimation();
        };
    }
}



