import * as PIXI from 'pixi.js';
import { BaseAnimation } from './baseAnimation';
import { AnimationDirection } from '../../shared/animation/animationDirection';
import { UsernameProperties } from '../../shared/animation/usernameProperties';
import { EventEmitter, Output } from '@angular/core';

const LEFT_SIDE = 0;
const RIGHT_SIDE = 12;

export abstract class PlayerAnimation extends BaseAnimation {

    protected upTextures: PIXI.Texture[];
    protected downTextures: PIXI.Texture[];
    protected sideTextures: PIXI.Texture[];
    protected usernameLabel: PIXI.Text;
    protected currentDirection: AnimationDirection;

    @Output()
    public animationDone: EventEmitter<any> = new EventEmitter();

    public constructor(upTextures: PIXI.Texture[], downTextures: PIXI.Texture[], sideTextures: PIXI.Texture[],
        username: string, position: PIXI.Point, gridDimension: PIXI.Point) {
        super(downTextures, gridDimension);
        this.upTextures = upTextures;
        this.downTextures = downTextures;
        this.sideTextures = sideTextures;
        this.position = position;

        this.usernameLabel = new PIXI.Text(username, UsernameProperties.STYLE);
        this.usernameLabel.x -= UsernameProperties.xOffset(this.usernameLabel.width);
        this.usernameLabel.position.y += UsernameProperties.Y_OFFSET;
        this.addChild(this.usernameLabel);
        this.actionName = '';
    }

    protected static loadTextures(color: string, actionName: string, nDownFrames: number, nSideFrames: number, nUpFrames: number): any {
        // Put resting frame for each animation except the walk one
        const downTextures = actionName === 'walk' ? [] : [PIXI.Texture.fromFrame(`walk_down_${color}_1.png`).clone()];
        const sideTextures = actionName === 'walk' ? [] : [PIXI.Texture.fromFrame(`walk_side_${color}_1.png`).clone()];
        const upTextures = actionName === 'walk' ? [] : [PIXI.Texture.fromFrame(`walk_up_${color}_1.png`).clone()];

        for (let i = 1; i <= nDownFrames; i++) {
            downTextures.push(PIXI.Texture.fromFrame(`${actionName}_down_${color}_${i}.png`).clone());
        }
        for (let i = 1; i <= nSideFrames; i++) {
            sideTextures.push(PIXI.Texture.fromFrame(`${actionName}_side_${color}_${i}.png`).clone());
        }
        for (let i = 1; i <= nUpFrames; i++) {
            upTextures.push(PIXI.Texture.fromFrame(`${actionName}_up_${color}_${i}.png`).clone());
        }

        return {
            down: downTextures,
            side: sideTextures,
            up: upTextures
        };
    }

    protected setDirection(direction: AnimationDirection): void {
        this.currentDirection = direction;
        switch (direction) {
            case AnimationDirection.UP:
                this.animation.textures = this.upTextures;
                break;
            case AnimationDirection.DOWN:
                this.animation.textures = this.downTextures;
                break;
            case AnimationDirection.LEFT:
                this.animation.textures = this.getRotatedSideTexture(LEFT_SIDE);
                break;
            case AnimationDirection.RIGHT:
                this.animation.textures = this.getRotatedSideTexture(RIGHT_SIDE);
                break;
            default:
                return;
        }
    }

    public updateTexture(newBaseTexture: PIXI.BaseTexture): void {
        this.downTextures.forEach(texture => {
            texture.baseTexture = newBaseTexture;
        });
        this.sideTextures.forEach(texture => {
            texture.baseTexture = newBaseTexture;
        });
        this.upTextures.forEach(texture => {
            texture.baseTexture = newBaseTexture;
        });
    }

    public endAnimation(): void {
        this.animation.gotoAndStop(0);
        this.animationDone.emit();
    }

    public execute(direction: AnimationDirection) {
        this.setDirection(direction);
        super.execute(direction);
    }

    public isPlaying(): boolean {
        return this.animation.playing;
    }

    private getRotatedSideTexture(rotationValue: number): PIXI.Texture[] {
        const flippedTexture = [];
        for (let i = 0; i < this.sideTextures.length; i++) {
            const rotatedTexture = this.sideTextures[i].clone();
            rotatedTexture.rotate = rotationValue;
            flippedTexture.push(rotatedTexture);
        }
        return flippedTexture;
    }
}



