import { Point } from 'pixi.js';
import { AnimationDirection } from '../../shared/animation/animationDirection';
import { EventEmitter, Output } from '@angular/core';
import { NoDirectionPlayerAnimation } from './noDirectionPlayerAnimation';

const NUMBER_DOWN_FRAMES = 5;
const NUMBER_SIDE_FRAMES = 0;
const NUMBER_UP_FRAMES = 0;

export class DeathAnimation extends NoDirectionPlayerAnimation {

    private spawnSpot: Point;
    @Output()
    public finishedAnimation: EventEmitter<any>;

    public constructor(username: string, position: Point, gridDimension: Point, color: string) {
        const textures = DeathAnimation.loadTextures(color, 'dramatic_death', NUMBER_DOWN_FRAMES, NUMBER_SIDE_FRAMES, NUMBER_UP_FRAMES);
        super(textures.up, textures.down, textures.side, username, position, gridDimension);
        this.actionName = 'dramatic_death';
        this.finishedAnimation = new EventEmitter();
        this.animation.onComplete = () => {
           this.startRespawn();
        };
    }

    public endAnimation(): void {
        this.animation.stop();
    }

   public startRespawn(): void {
        this.endAnimation();
        this.finishedAnimation.emit(this.spawnSpot);
   }

    public execute(direction: AnimationDirection) {
        this.animation.gotoAndPlay(0);
    }

    public setSpawnSpot( spawnSpot: Point): void {
        this.spawnSpot = spawnSpot;
    }
}



