import { Texture } from 'pixi.js';
import { UpgradeType } from '../../classes/items';
import { PlayerAnimation } from './playerAnimation';
import { AnimationDirection } from '../../shared/animation/animationDirection';

const NUMBER_FRAMES = 3;
const NUMBER_DOWN_FRAMES = 0;
const NUMBER_SIDE_FRAMES = 0;
const NUMBER_UP_FRAMES = 0;

export class UpgradeAnimation extends PlayerAnimation {

    protected backpackTexture: Texture[];
    protected swordTexture: Texture[];
    protected shieldTexture: Texture[];
    protected heartTexture: Texture[];
    protected pickaxeTexture: Texture[];
    protected potionTexture: Texture[];
    protected emptyHandTexture: Texture[];

    public constructor(username: string, position: PIXI.Point, gridDimension: PIXI.Point, color: string) {
        const textures = UpgradeAnimation.loadTextures(color, 'upgrade', NUMBER_DOWN_FRAMES, NUMBER_SIDE_FRAMES, NUMBER_UP_FRAMES);
        super([], textures.emptyHand, [], username, position, gridDimension); // give sprite a default texture
        this.actionName = 'upgrade';

        this.backpackTexture = textures.bag;
        this.swordTexture = textures.sword;
        this.shieldTexture = textures.shield;
        this.heartTexture = textures.heart;
        this.pickaxeTexture = textures.pickaxe;
        this.potionTexture = textures.potion;
        this.emptyHandTexture = textures.emptyHand;

        this.animation.animationSpeed = 0.05;
        this.animation.onComplete = () => {
            this.endAnimation();
        };
    }

    protected static loadTextures(color: string, actionName: string, nDownFrames: number, nSideFrames: number, nUpFrames: number): any {

        const restFrame = PIXI.Texture.fromFrame(`walk_down_${color}_1.png`).clone();
        const bagTexture = [restFrame] as Array<Texture>;
        const swordTexture = [restFrame] as Array<Texture>;
        const shieldTexture = [restFrame] as Array<Texture>;
        const heartTexture = [restFrame] as Array<Texture>;
        const pickaxeTexture = [restFrame] as Array<Texture>;
        const potionTexture = [restFrame] as Array<Texture>;
        const emptyHandTexture = [restFrame] as Array<Texture>;

        for (let i = 1; i <= NUMBER_FRAMES; i++) {
            bagTexture.push(PIXI.Texture.fromFrame(`${actionName}_bag_${color}_${i}.png`).clone());
            swordTexture.push(PIXI.Texture.fromFrame(`${actionName}_sword_${color}_${i}.png`).clone());
            shieldTexture.push(PIXI.Texture.fromFrame(`${actionName}_shield_${color}_${i}.png`).clone());
            heartTexture.push(PIXI.Texture.fromFrame(`${actionName}_heart_${color}_${i}.png`).clone());
            pickaxeTexture.push(PIXI.Texture.fromFrame(`${actionName}_pickaxe_${color}_${i}.png`).clone());
            potionTexture.push(PIXI.Texture.fromFrame(`${actionName}_potion_${color}_${i}.png`).clone());
            emptyHandTexture.push(PIXI.Texture.fromFrame(`${actionName}_empty_${color}_${i}.png`).clone());
        }
        return {
            bag: bagTexture,
            sword: swordTexture,
            shield: shieldTexture,
            heart: heartTexture,
            pickaxe: pickaxeTexture,
            potion: potionTexture,
            emptyHand: emptyHandTexture
        };
    }

    public setUpgradeType(upgradeType: UpgradeType, hasItem: boolean): void {
        switch (upgradeType) {
            case UpgradeType.AttackPower:
                this.animation.textures = hasItem ? this.swordTexture : this.emptyHandTexture;
                break;
            case UpgradeType.CarryingCapacity:
                this.animation.textures = hasItem ? this.backpackTexture : this.emptyHandTexture;
                break;
            case UpgradeType.CollectingSpeed:
                this.animation.textures = hasItem ? this.pickaxeTexture : this.emptyHandTexture;
                break;
            case UpgradeType.Defence:
                this.animation.textures = hasItem ? this.shieldTexture : this.emptyHandTexture;
                break;
            case UpgradeType.MaximumHealth:
                this.animation.textures = this.heartTexture;
                break;
            case UpgradeType.Potion:
                this.animation.textures = this.potionTexture;
                break;
            default:
                return;
        }
    }

    public updateTexture(newBaseTexture: PIXI.BaseTexture): void {}

    protected setDirection(direction: AnimationDirection) {}
}



