export abstract class UsernameProperties {
    public static readonly STYLE = { fontFamily: 'Arial', fontSize: '1.2em', align: 'center', fill: 'white'  };
    public static readonly Y_OFFSET = 5;

    public static xOffset(usernameWidth: number): number {
        return usernameWidth / 4;
    }
}



