import {Point, BaseTexture} from 'pixi.js';
import { WalkAnimation } from '../game/animation/walkAnimation';
import { CharacterInfo } from './characterInfo';
import { MiningAnimation } from '../game/animation/miningAnimation';
import { MeleeAttackAnimation } from '../game/animation/meleeAttackAnimation';
import { IAction } from '../shared/actions/iAction';
import { CollectAction } from '../shared/actions/collectAction';
import { MeleeAttackAction } from '../shared/actions/meleeAttackAction';
import { DeathAnimation } from '../game/animation/deathAnimation';
import { MoveAction } from '../shared/actions/moveAction';
import { HealingAnimation } from '../game/animation/healAnimation';
import { UpgradeType, PurchasableItem, Item } from './items';
import { UpgradeAction } from '../shared/actions/upgradeAction';
import { UpgradeAnimation } from '../game/animation/upgradeAnimation';
import { PurchaseAction } from '../shared/actions/purchaseAction';
import mergeImages from 'merge-images';
import { ISubscription } from 'rxjs/Subscription';
import { ICharacter } from './iCharacter';

const MOVE_ACTION = 'LHGames.Actions.MoveAction';
const COLLECT_ACTION = 'LHGames.Actions.CollectAction';
const MELEE_ATTACK_ACTION = 'LHGames.Actions.MeleeAttackAction';
const HEAL_ACTION = 'LHGames.Actions.HealAction';
const UPGRADE_ACTION = 'LHGames.Actions.UpgradeAction';
const PURCHASE_ACTION = 'LHGames.Actions.PurchaseAction';

export class Player extends ICharacter {

    private walkAnimation: WalkAnimation;
    private deathAnimation: DeathAnimation;
    private meleeAttackAnimation: MeleeAttackAnimation;
    private miningAnimation: MiningAnimation;
    private healingAnimation: HealingAnimation;
    private upgradeAnimation: UpgradeAnimation;
    private color: string;
    private items: PurchasableItem[] = [];

    public constructor(info: CharacterInfo, positionModifier: Point, scaleModifier: Point,
                        color: string, totalMapSize: Point) {
        super(info, positionModifier, scaleModifier, color, totalMapSize);
        this.color = color;
    }

    public animate(action: IAction): void {
        if (!action) {
            return;
        }
        for (const itemServer of action.source.items) {
            const item = Item.getItem(itemServer.name);
            // No special texture added to the spritesheet for those items
            if (item !== PurchasableItem.HealthPotion && item !== PurchasableItem.Backpack) {
                if (!Object.values(this.items).includes(item)) {
                    this.updateTexture(item);
                }
            }
        }
        this.respawn(this.characterPosition);
        this.checkIfDying();
        switch (action.type) {
            case MOVE_ACTION:
                const moveAction = action as MoveAction;
                this.walk(moveAction.direction);
                break;
            case COLLECT_ACTION:
                const collectAction = action as CollectAction;
                this.mining(collectAction.direction);
                break;
            case MELEE_ATTACK_ACTION:
                const meleeAttackAction = action as MeleeAttackAction;
                this.meleeAttack(meleeAttackAction.direction);
                break;
            case HEAL_ACTION:
                this.heal();
                break;
            case UPGRADE_ACTION:
                const upgradeAction = action as UpgradeAction;
                this.upgrade(upgradeAction.upgradeType);
                break;
            case PURCHASE_ACTION:
                const purchaseAction = action as PurchaseAction;
                this.buy(purchaseAction.item);
                break;
            default:
                break;
        }
    }

    public respawn(position: Point): void {
        this.info.position = position;
        this.updateCharacterPosition(this.info.position);
        this.walkAnimation.reset();
        this.setCurrentAnimation(this.walkAnimation);
    }

    public die(point: Point): void {
        if (this.deathAnimation === undefined) {
            return;
        }
        if (this.currentAnimation.isPlaying()) {
           const subscription =  this.currentAnimation.animationDone.subscribe(_ => {
                    subscription.unsubscribe();
                    this.die(point);
            }) as ISubscription;
            return;
        }
        if (this.currentAnimation === undefined || this.currentAnimation !== this.deathAnimation) {
            this.setCurrentAnimation(this.deathAnimation);
        }
        this.deathAnimation.setSpawnSpot(point);
        this.currentAnimation.execute(null);
    }

    private updateTexture(item: PurchasableItem): void {
        this.items.push(item);
        const itemUrl = Item.getUrl(item);
        if (itemUrl === null) {
            return;
        }
        mergeImages([ `../../assets/images/players/link_${this.color}.png`, itemUrl])
            .then(url => this.updateAnimationTextures(url));
    }

    private updateAnimationTextures(imageUrl: string): void {
        const baseTexture = BaseTexture.fromImage(imageUrl);
        this.walkAnimation.updateTexture(baseTexture);
        this.healingAnimation.updateTexture(baseTexture);
        this.meleeAttackAnimation.updateTexture(baseTexture);
        this.miningAnimation.updateTexture(baseTexture);
        this.deathAnimation.updateTexture(baseTexture);
    }

    private walk(direction: Point): void {
        if (this.walkAnimation === undefined) {
            return;
        }
        if (this.currentAnimation === undefined || this.currentAnimation !== this.walkAnimation) {
            this.setCurrentAnimation(this.walkAnimation);
        }
        this.playAnimation(direction);
        this.move(direction);
    }

    private mining(direction: Point): void {
        if (this.miningAnimation === undefined) {
            return;
        }
        if (this.currentAnimation === undefined || this.currentAnimation !== this.miningAnimation) {
            this.setCurrentAnimation(this.miningAnimation);
        }
        this.playAnimation(direction);
    }

    private meleeAttack(direction: Point): void {
        if (this.meleeAttackAnimation === undefined) {
            return;
        }
        if (this.currentAnimation === undefined || this.currentAnimation !== this.meleeAttackAnimation) {
            this.setCurrentAnimation(this.meleeAttackAnimation);
        }
        this.playAnimation(direction);
    }

    private heal(): void {
        if (this.healingAnimation === undefined) {
            return;
        }
        if (this.currentAnimation === undefined || this.currentAnimation !== this.healingAnimation) {
            this.setCurrentAnimation(this.healingAnimation);
        }
        this.currentAnimation.execute(null);
    }

    private upgrade(upgradeType: UpgradeType): void {
        if (this.upgradeAnimation === undefined) {
            return;
        }
        const purchaseItem = Item.convertToPurchase(upgradeType);
        const hasItem = Object.values(this.items).includes(purchaseItem);
        this.upgradeAnimation.setUpgradeType(upgradeType, hasItem);

        if (this.currentAnimation === undefined || this.currentAnimation !== this.upgradeAnimation) {
            this.setCurrentAnimation(this.upgradeAnimation);
        }
        this.currentAnimation.execute(null);
    }

    private buy(item: PurchasableItem): void {
        this.updateTexture(item);
        this.upgrade(Item.convertToUpgrade(item));
    }

    private checkIfDying(): void {
        if  (this.currentAnimation.isPlaying() && this.currentAnimation === this.deathAnimation) {
            this.deathAnimation.startRespawn();
        }
    }

    protected createAnimations(scaleModifier: Point, color: string): void {
        this.deathAnimation = new DeathAnimation(this.info.name, this.spritePosition, scaleModifier, color);
        this.deathAnimation.finishedAnimation.subscribe(spawnSpot => {
            this.respawn(spawnSpot);
        });
        this.upgradeAnimation = new UpgradeAnimation(this.info.name, this.spritePosition, scaleModifier, color);
        this.miningAnimation = new MiningAnimation(this.info.name, this.spritePosition, scaleModifier, color);
        this.meleeAttackAnimation = new MeleeAttackAnimation(this.info.name, this.spritePosition, scaleModifier, color);
        this.healingAnimation = new HealingAnimation(this.info.name, this.spritePosition, scaleModifier, color);
        this.walkAnimation = new WalkAnimation(this.info.name, this.spritePosition, scaleModifier, color);
        this.currentAnimation = this.walkAnimation;
    }

    protected updateAnimation(): void {
        if (this.healingAnimation) {
            this.healingAnimation.update(this.spritePosition, this.scaleModifier);
        }
        if (this.meleeAttackAnimation) {
            this.meleeAttackAnimation.update(this.spritePosition, this.scaleModifier);
        }
        if (this.miningAnimation) {
            this.miningAnimation.update(this.spritePosition, this.scaleModifier);
        }
        if (this.walkAnimation) {
            this.walkAnimation.update(this.spritePosition, this.scaleModifier);
        }
        if (this.deathAnimation) {
            this.deathAnimation.update(this.spritePosition, this.scaleModifier);
        }
        if (this.upgradeAnimation) {
            this.upgradeAnimation.update(this.spritePosition, this.scaleModifier);
        }
    }
}



