import * as PIXI from 'pixi.js';
import { TileType } from './tiletype';
import { ITile } from '../shared/messages/syncMessage';

const PNG_RESOLUTION = 128;

export class Map {
    private mapInfo: PIXI.Rectangle;
    private container: PIXI.Container;
    private grassBackground: PIXI.extras.TilingSprite;
    private appScreen: PIXI.Rectangle;

    public getMapContainer(): PIXI.Container {
        return this.container;
    }

    public getGrassBackground(): PIXI.Container {
        return this.grassBackground;
    }

    public getCentralPosition(): PIXI.Point {
        return new PIXI.Point(this.mapInfo.x, this.mapInfo.y);
    }

    public getMapInfo(): PIXI.Rectangle {
        return this.mapInfo;
    }

    public constructor(screen: PIXI.Rectangle, height: number, width: number, center: PIXI.Point) {
        this.appScreen = screen;
        this.mapInfo = new PIXI.Rectangle(center.x, center.y, width, height);
        this.container = new PIXI.Container();
        this.initGrassBackground();
    }

    public update(tiles: ITile[][], playerSpritesheet: string[]): boolean {
        if (!tiles || tiles.length === 0 || tiles[0].length === 0) {
            return false;
        }

        const hasChanged = this.updateSize(tiles.length, tiles[0].length);

        this.container.removeChildren();
        for (let i = 0; i < tiles.length; i++) {
            for (let j = 0; j < tiles[i].length; j++) {
                let sprite: PIXI.Sprite;
                switch (tiles[i][j].content) {
                    case TileType.House:
                        const guid = tiles[i][j].data;
                        sprite = new PIXI.Sprite(PIXI.loader.resources['house_' + playerSpritesheet[guid]].texture);
                        break;
                    case TileType.Lava:
                        sprite = new PIXI.Sprite(PIXI.loader.resources.lava.texture);
                        break;
                    case TileType.Resource:
                        if (tiles[i][j].data.toLowerCase() === 'true') {
                            sprite = new PIXI.Sprite(PIXI.loader.resources.highDensityResource.texture);
                        } else {
                            sprite = new PIXI.Sprite(PIXI.loader.resources.resource.texture);
                        }
                        break;
                    case TileType.Shop:
                        sprite = new PIXI.Sprite(PIXI.loader.resources.shop.texture);
                        break;
                    case TileType.Wall:
                        sprite = new PIXI.Sprite(PIXI.loader.resources.wall.texture);
                        break;
                    case TileType.Player: // animatedSprite is used
                    case TileType.Tile: // Nothing to show
                    default:
                        break;
                }
                if (sprite !== undefined) {
                    sprite.anchor.x = 0.5;
                    sprite.anchor.y = 0.5;
                    sprite.scale.set(Math.round(this.appScreen.width / this.mapInfo.width) / sprite.width);
                    sprite.x = i * Math.round(this.appScreen.height / this.mapInfo.height);
                    sprite.y = (j % this.mapInfo.width) * Math.round(this.appScreen.width / this.mapInfo.width);
                    this.container.addChild(sprite);
                }
            }
        }

        return hasChanged;
    }

    public onResize(newSize: PIXI.Point): void {
        this.appScreen.width = newSize.x;
        this.appScreen.height = newSize.y;
        this.initGrassBackground();
    }

    public setCentralPosition(position: PIXI.Point): boolean {
        const hasChanged = this.mapInfo.x !== position.x || this.mapInfo.y !== position.y;
        if (hasChanged) {
            this.mapInfo.x = position.x;
            this.mapInfo.y = position.y;
        }
        return hasChanged;
    }

    private initGrassBackground(): void {
        this.grassBackground = new PIXI.extras.TilingSprite(
            PIXI.loader.resources.grass.texture,
            this.appScreen.width,
            this.appScreen.height
        );
        this.grassBackground.cacheAsBitmap = true;
        const scale = Math.ceil(this.mapInfo.width / (this.appScreen.width / PNG_RESOLUTION));
        this.grassBackground.tileScale.set(1 / scale, 1 / scale);
    }

    private updateSize(height: number, width: number): boolean {
        const hasChanged = this.mapInfo.width !== width || this.mapInfo.height !== height;
        if (hasChanged) {
            this.mapInfo.width = width;
            this.mapInfo.height = height;
        }
        return hasChanged;
    }
}



