import { PlayerAnimation } from './playerAnimation';

const NUMBER_DOWN_FRAMES = 9;
const NUMBER_SIDE_FRAMES = 8;
const NUMBER_UP_FRAMES = 7;

export class MeleeAttackAnimation extends PlayerAnimation {

    public constructor(username: string, position: PIXI.Point, gridDimension: PIXI.Point, color: string) {
        const textures = MeleeAttackAnimation.loadTextures(color, 'melee', NUMBER_DOWN_FRAMES, NUMBER_SIDE_FRAMES, NUMBER_UP_FRAMES);
        super(textures.up, textures.down, textures.side, username, position, gridDimension);
        this.actionName = 'melee';
        this.animation.onComplete = () => {
            this.endAnimation();
        };
    }
}



