import { Component, OnInit, ViewChild, ElementRef, HostListener, Output, EventEmitter } from '@angular/core';
import { Scene } from '../classes/scene';
import { GameService } from './game.service';
import { SyncMessage } from '../shared/messages/syncMessage';
import { SpriteSheetColor } from '../classes/spritesheetColor';

const VIEW_TRANSLATION = 4;
const VIEW_ZOOM = 4;
const LEFT_KEYCODE = 37;        // Left arrow
const UP_KEYCODE = 38;          // Up arrow
const RIGHT_KEYCODE = 39;       // Right arrow
const DOWN_KEYCODE = 40;        // Down arrow
const ZOOM_IN_KEYCODE = 65;     // a
const ZOOM_OUT_KEYCODE = 83;    // s

@Component({
    selector: 'app-game',
    templateUrl: './game.component.html',
    styleUrls: ['./game.component.css']
})

export class GameComponent implements OnInit {

    @Output()
    public sceneReady: EventEmitter<any> = new EventEmitter();

    private scene: Scene;
    private isGameSceneReady = false;
    private sceneSize: PIXI.Point;
    private lastSyncTimestamp: Date;
    private forceSync = false;

    @ViewChild('gameView')
    private gameViewRef: ElementRef;

    public constructor(private gameService: GameService) { }

    public async ngOnInit(): Promise<void> {
        this.gameService.getSpritesheets().subscribe(spritesheetsData => {
            if (!this.scene) {
                const spritesheets = this.parseSpritesheet(spritesheetsData.spritesheets);
                this.initScene(spritesheets);
            } else {
                // update spritesheet and player concerned i guess?
            }
        });
        this.gameService.getMapInfo().subscribe(mapData => {
            this.lastSyncTimestamp = mapData.timestamp;
            if (this.isGameSceneReady) {
                this.resyncScene(mapData);
            }
        });
        this.gameService.getTurn().subscribe(turn => {
            if (this.isGameSceneReady && turn.timestamp > this.lastSyncTimestamp) {
                this.scene.playAnimations(turn.actions);
                this.scene.respawnDeadPlayers(turn.deadPlayers);
            }
        });
    }

    private initScene(spritesheets: string[]): void {
        if (this.sceneSize) {
            this.scene = new Scene(spritesheets, this.sceneSize);

            this.scene.sceneLoaded.subscribe(_ => {
                this.gameService.requestNewView(this.scene.getMapInfo(), this.scene.getMapCentralPosition());
                this.forceSync = true;
                this.isGameSceneReady = true;
                setTimeout(() => {
                    this.sceneReady.emit();
                }, 10);
            });

            this.gameViewRef.nativeElement.appendChild(this.scene.view);
        } else {
            setTimeout(_ => this.initScene(spritesheets), 100);
        }
    }

    public onResize(width: number, height: number): void {
        this.sceneSize = new PIXI.Point(width, height);
        // TODO: limit number of resize requests, could overwhelm the server.
        // (Wait until window is done resizing?)
        if (this.scene) {
            this.scene.onResize(this.sceneSize);
            // Hack to refresh view.
            const newPosition = this.scene.getMapCentralPosition();
            const newViewSize = this.scene.getMapInfo();
            this.gameService.requestNewView(newViewSize, newPosition);
            this.forceSync = true;
        }
    }

    public moveView(direction: string): void {
        if (!this.scene) {
            return;
        }

        const newPosition = this.scene.getMapCentralPosition();
        switch (direction) {
            case 'up':
                newPosition.y -= VIEW_TRANSLATION;
                break;
            case 'down':
                newPosition.y += VIEW_TRANSLATION;
                break;
            case 'left':
                newPosition.x -= VIEW_TRANSLATION;
                break;
            case 'right':
                newPosition.x += VIEW_TRANSLATION;
                break;
            default:
                break;
        }
        const newViewSize = this.scene.getMapInfo();
        this.gameService.requestNewView(newViewSize, newPosition);
    }

    public zoom(direction: string): void {
        const newPosition = this.scene.getMapCentralPosition();
        const newViewSize = this.scene.getMapInfo().clone();
        if (direction === 'out') {
            newViewSize.height += VIEW_ZOOM;
            newViewSize.width += VIEW_ZOOM;
        } else {
            newViewSize.height -= VIEW_ZOOM;
            newViewSize.width -= VIEW_ZOOM;
        }
        this.gameService.requestNewView(newViewSize, newPosition);
    }

    @HostListener('document:keyup', ['$event'])
    public onKeyDown(event: any): void {
        switch (event.keyCode) {
            case LEFT_KEYCODE:
                this.moveView('left');
                break;
            case UP_KEYCODE:
                this.moveView('up');
                break;
            case RIGHT_KEYCODE:
                this.moveView('right');
                break;
            case DOWN_KEYCODE:
                this.moveView('down');
                break;
            case ZOOM_IN_KEYCODE:
                this.zoom('in');
                break;
            case ZOOM_OUT_KEYCODE:
                this.zoom('out');
                break;
        }
    }

    private resyncScene(syncMessage: SyncMessage): void {
        const map = syncMessage.map;
        const centralPositionChanged = this.scene.setMapCentralPosition(syncMessage.position);
        const mapChanged = this.scene.updateMap(map);
        if (this.forceSync || centralPositionChanged || mapChanged) {
            this.scene.updatePlayers(syncMessage.players, syncMessage.totalMapSize);
            this.forceSync = false;
        }
    }

    private parseSpritesheet(playerSpritesheet: string[]): string[] {
        const parsedSpriteSheet = [];
        for (const playerGuid in playerSpritesheet) {
            if (playerSpritesheet[playerGuid] !== undefined) {
                const colorNumber = parseInt(playerSpritesheet[playerGuid], 10);
                parsedSpriteSheet[playerGuid] = SpriteSheetColor[colorNumber];
            }
        }
        return parsedSpriteSheet;
    }
}



