import * as PIXI from 'pixi.js';
import { Map } from './map';
import { Player } from '../classes/player';
import { IAction } from '../shared/actions/iAction';
import { CharacterInfo } from './characterInfo';
import { EventEmitter, Output } from '@angular/core';
import { ITile } from '../shared/messages/syncMessage';
import { DeadPlayer } from '../shared/messages/turnMessage';
import { Goblin } from './goblin';
import { ICharacter } from './iCharacter';

const TILES_PER_ROW_DEFAULT = 31;
const TILES_PER_COLUMN_DEFAULT = 31;
const MIDDLE_POSITION_DEFAULT = 16;
const PLAYER_JSON_PATH = 'assets/jsons/link_';
const UPGRADE_JSON_PATH = 'assets/jsons/upgrade_';
const HOUSE_PNG_PATH = 'assets/images/tiles/house_';

const assets = [
    { name: 'grass', path: 'assets/images/tiles/grass.png' },
    { name: 'wall', path: 'assets/images/tiles/wall.png' },
    { name: 'lava', path: 'assets/images/tiles/lava.png' },
    { name: 'resource', path: 'assets/images/tiles/resource.png' },
    { name: 'highDensityResource', path: 'assets/images/tiles/highDensityResource.png' },
    { name: 'shop', path: 'assets/images/tiles/shop.png' },
    { name: 'goblin', path: 'assets/jsons/enemy.json'}
];

export class Scene {

    private app: PIXI.Application;
    private map: Map;
    private players: ICharacter[] = [];

    @Output()
    public sceneLoaded: EventEmitter<any> = new EventEmitter();

    public get view(): HTMLCanvasElement {
        return this.app.view;
    }

    public constructor(private playerSpritesheets: string[], sceneSize: PIXI.Point) {
        this.app = new PIXI.Application({
            width: sceneSize.x,
            height: sceneSize.y,
            antialias: true,
            transparent: false,
            resolution: 1
        });

        this.loadAssets();
    }

    public onResize(newSize: PIXI.Point): void {
        this.app.renderer.resize(newSize.x, newSize.y);
        this.app.stage.removeChild(this.map.getGrassBackground());
        this.map.onResize(newSize);
        const grass = this.map.getGrassBackground();
        this.app.stage.addChildAt(grass, 0);
    }

    public updateMap(mapData: ITile[][]): boolean {
        if (this.map) {
            return this.map.update(mapData, this.playerSpritesheets);
        }
        return false;
    }

    public setMapCentralPosition(position: PIXI.Point): boolean {
        if (this.map) {
            return this.map.setCentralPosition(position);
        }
        return false;
    }

    public getMapCentralPosition(): PIXI.Point {
        return this.map.getCentralPosition();
    }

    public getMapInfo(): PIXI.Rectangle {
        return this.map.getMapInfo();
    }

    public playAnimations(actions: IAction[]): void {
        for (const action of actions) {
            const playerId = action.source.playerID as string;
            if (this.isValidPlayer(playerId)) {
                this.players[playerId].animate(action);
            }
        }
    }

    public respawnDeadPlayers(deadPlayers: DeadPlayer[]): void {
        for (const deadPlayer of deadPlayers) {
            if (this.isValidPlayer(deadPlayer.playerId)) {
                this.players[deadPlayer.playerId].die(deadPlayer.houseLocation);
            }
        }
    }

    public updatePlayers(playersInfos: CharacterInfo[], totalMapSize: PIXI.Point): void {
        const mapInfo = this.getMapInfo();
        const spriteSize = new PIXI.Point(
            Math.round(this.app.screen.height / mapInfo.height),
            Math.round(this.app.screen.width / mapInfo.width)
        );
        const positionModifier = new PIXI.Point(
            mapInfo.x - Math.floor(mapInfo.width / 2), Math.ceil(mapInfo.y - mapInfo.height / 2)
        );

        for (const playerGuid in playersInfos) {
            if (this.players[playerGuid]) {
                this.players[playerGuid].update(playersInfos[playerGuid], positionModifier, spriteSize, totalMapSize);
            } else {
                const newPlayer = this.playerSpritesheets[playerGuid] ?
                    new Player(playersInfos[playerGuid], positionModifier, spriteSize, this.playerSpritesheets[playerGuid], totalMapSize) :
                    new Goblin(playersInfos[playerGuid], positionModifier, spriteSize, totalMapSize);

                if (!this.playerSpritesheets[playerGuid]) {
                    (newPlayer as Goblin).haveToRemove.subscribe(_ => {
                        this.app.stage.removeChild(newPlayer.getCurrentAnimation());
                        const index = this.players.indexOf(newPlayer, 0);
                        if (index > -1) {
                        this.players.splice(index, 1);
                        }
                    });
                }
                newPlayer.changeAnimation.subscribe(animationObject => {
                    this.app.stage.removeChild(animationObject.oldAnimation);
                    this.app.stage.addChild(animationObject.newAnimation);
                });
                this.players[playerGuid] = newPlayer;
                this.app.stage.addChild(newPlayer.getCurrentAnimation());
            }
        }
    }

    private loadAssets(): void {
        const addedSpritesheet = [];
        for (const playerGuid in this.playerSpritesheets) {
            if (this.playerSpritesheets[playerGuid]) {
                const spritesheetColor = this.playerSpritesheets[playerGuid];
                if (!addedSpritesheet.includes(spritesheetColor)) {
                    addedSpritesheet.push(spritesheetColor);
                    PIXI.loader.add('player_' + spritesheetColor, `${PLAYER_JSON_PATH}${spritesheetColor}.json`);
                    PIXI.loader.add('house_' + spritesheetColor, `${HOUSE_PNG_PATH}${spritesheetColor}.png`);
                    PIXI.loader.add('upgrade_' + spritesheetColor, `${UPGRADE_JSON_PATH}${spritesheetColor}.json`);
                }
            }
        }

        for (const asset of assets) {
            PIXI.loader.add(asset.name, asset.path);
        }

        PIXI.loader.load(() => {
            this.onLoaded();
        });
    }

    private onLoaded(): void {
        this.map = new Map(this.app.screen, TILES_PER_COLUMN_DEFAULT, TILES_PER_ROW_DEFAULT,
            new PIXI.Point(MIDDLE_POSITION_DEFAULT, MIDDLE_POSITION_DEFAULT));
        this.app.stage.addChild(this.map.getGrassBackground());
        this.app.stage.addChild(this.map.getMapContainer());
        this.sceneLoaded.emit();
    }

    private isValidPlayer(playerGuid: string): boolean {
        return this.players[playerGuid] !== undefined;
    }
}



