import { MessageType } from './messageType';
import { IMessage } from './iMessage';
import * as PIXI from 'pixi.js';

export class UpdateViewMessage implements IMessage {
    timestamp: Date;
    type: MessageType;
    viewSize: PIXI.Point;
    position: PIXI.Point;

    public constructor(viewSize: PIXI.Rectangle, location: PIXI.Point) {
        this.type = MessageType.Update;
        this.position = location;
        this.viewSize = { x: viewSize.width, y: viewSize.height } as PIXI.Point;
        this.timestamp = new Date();
    }
}



