import { PlayerAnimation } from './playerAnimation';

const NUMBER_DOWN_FRAMES = 4;
const NUMBER_SIDE_FRAMES = 5;
const NUMBER_UP_FRAMES = 6;

export class MiningAnimation extends PlayerAnimation {

    public constructor(username: string, position: PIXI.Point, gridDimension: PIXI.Point, color: string) {
        const textures = MiningAnimation.loadTextures(color, 'mine', NUMBER_DOWN_FRAMES, NUMBER_SIDE_FRAMES, NUMBER_UP_FRAMES);
        super(textures.up, textures.down, textures.side, username, position, gridDimension);
        this.actionName = 'mine';
        this.animation.animationSpeed = 0.1;
        this.animation.onComplete = () => {
            this.endAnimation();
        };
    }
}



