import * as PIXI from 'pixi.js';
import { AnimationDirection } from '../../shared/animation/animationDirection';

export abstract class BaseAnimation extends PIXI.Container {

    public actionName: string;
    protected initialSize: PIXI.Rectangle;
    protected animation: PIXI.extras.AnimatedSprite;

    protected constructor(textures: PIXI.Texture[], gridDimension: PIXI.Point) {
        super();
        this.animation = new PIXI.extras.AnimatedSprite(textures);
        this.animation.loop = false;
        this.animation.animationSpeed = 0.2;
        this.addChild(this.animation);
        this.initialSize = new PIXI.Rectangle(null, null, this.width, this.height);
        this.updateScale(gridDimension);
        this.animation.anchor.x = 0.5;
        this.animation.anchor.y = 0.5;
    }

    protected translateAnimation(): void { }

    public execute(direction: AnimationDirection): void {
        this.animation.play();
    }

    public update(newPosition: PIXI.Point, gridDimension: PIXI.Point): void {
        this.updateScale(gridDimension);
        this.position = newPosition;
    }

    protected updateScale(gridDimension: PIXI.Point): void {
        this.scale.x = gridDimension.x / this.initialSize.width;
        this.scale.y = gridDimension.y / this.initialSize.height;
    }
}



