export enum PurchasableItem {
    Sword,
    Shield,
    Backpack,
    Pickaxe,
    HealthPotion
}

export enum UpgradeType {
    CarryingCapacity, // BackPack
    AttackPower, // Sword
    Defence, // Shield
    MaximumHealth, // Heart
    CollectingSpeed, // Pickaxe
    Potion // Potion added for animation
}

export enum ItemURL {
    Sword = '../../assets/images/items/sword.png',
    Shield = '../../assets/images/items/shield.png',
    // Backpack = '../../assets/images/items/backpack.png',
    Pickaxe = '../../assets/images/items/pickaxe.png'
}

export class Item {
    public static convertToUpgrade(item: PurchasableItem): UpgradeType {
        switch (item) {
            case PurchasableItem.Backpack:
                return UpgradeType.CarryingCapacity;
            case PurchasableItem.HealthPotion:
                return UpgradeType.Potion;
            case PurchasableItem.Pickaxe:
                return UpgradeType.CollectingSpeed;
            case PurchasableItem.Shield:
                return UpgradeType.Defence;
            case PurchasableItem.Sword:
                return UpgradeType.AttackPower;
            default:
                break;
        }
    }

    public static convertToPurchase(item: UpgradeType): PurchasableItem {
        switch (item) {
            case UpgradeType.CarryingCapacity:
                return PurchasableItem.Backpack;
            case UpgradeType.Potion:
                return PurchasableItem.HealthPotion;
            case UpgradeType.CollectingSpeed:
                return PurchasableItem.Pickaxe;
            case UpgradeType.Defence:
                return PurchasableItem.Shield;
            case UpgradeType.AttackPower:
                return PurchasableItem.Sword;
            default:
                break;
        }
    }

    public static getUrl(item: PurchasableItem): string {
        switch (item) {
            // case PurchasableItem.Backpack:
            //     return ItemURL.Backpack;
            case PurchasableItem.Pickaxe:
                return ItemURL.Pickaxe;
            case PurchasableItem.Shield:
                return ItemURL.Shield;
            case PurchasableItem.Sword:
                return ItemURL.Sword;
            default:
                return null;
        }
    }

    public static getItem(itemName: string): PurchasableItem {
        switch (itemName) {
            case 'Backpack':
                return PurchasableItem.Backpack;
            case 'Pickaxe':
                return PurchasableItem.Pickaxe;
            case 'Shield':
                return PurchasableItem.Shield;
            case 'Sword':
                return PurchasableItem.Sword;
            case 'Health Potion':
                return PurchasableItem.Backpack;
            default:
                return null;
        }
    }
}



