import { Component, OnInit, HostListener } from '@angular/core';
import { IPlayerInfo } from './player-info';
import { LeaderboardService } from './leaderboard.service';
import { Observable } from 'rxjs/Observable';


const GODMODE_KEY = 71; // g
@Component({
    selector: 'app-leaderboard',
    templateUrl: './leaderboard.component.html',
    styleUrls: ['./leaderboard.component.css']
})
export class LeaderboardComponent implements OnInit {
    public bindedId: string;
    private isGodMode: boolean;
    private players: IPlayerInfo[];
    private currentIndex: number;

    public constructor(public leaderboardService: LeaderboardService) {
        this.isGodMode = false;
        this.currentIndex = 0;
        this.leaderboardService.getPlayersInfo()
            .subscribe((info) => {
                this.players = info;
            });
    }

    public ngOnInit(): void {
        setInterval(() => this.switchPlayer(), 15000);
    }

    public followPlayer(player: IPlayerInfo): void {
        if (this.bindedId && player.playerId === this.bindedId) {
            this.unFollowPlayer();
        } else {
            this.bindedId = player.playerId;
            this.leaderboardService.bindToPlayer(this.bindedId);
        }
        this.isGodMode = false;
    }

    private unFollowPlayer(): void {
        this.bindedId = undefined;
        this.leaderboardService.unbindToPlayer();
    }

    private switchPlayer(): void {
        if (this.isGodMode && this.players) {
            this.currentIndex = Math.floor(Math.random() * this.players.length);
            const player = this.players[this.currentIndex];
            this.bindedId = player.playerId;
            this.leaderboardService.bindToPlayer(this.bindedId);
            this.currentIndex %= this.players.length;
        }
    }

    @HostListener('window:keydown', ['$event'])
    public onKeyDown(event: KeyboardEvent): void {
        if (event.keyCode === GODMODE_KEY) {
            this.isGodMode = !this.isGodMode;
        }
    }
}



