import { Injectable } from '@angular/core';
import { IPlayerInfo } from './player-info';
import { Observable } from 'rxjs/Observable';
import { WebSocketService } from '../shared/providers/web-socket.service';
import { filter, map } from 'rxjs/operators';
import { MessageType } from '../shared/messages/messageType';
import { IMessage } from '../shared/messages/iMessage';
import { LeaderboardMessage } from '../shared/messages/leaderboardMessage';
import { BindPlayerMessage } from '../shared/messages/bindPlayerMessage';

@Injectable()
export class LeaderboardService {

    private readonly leaderboardData$: Observable<IPlayerInfo[]>;

    public constructor(private webSocketService: WebSocketService) {
        this.leaderboardData$ = this.webSocketService.message$.pipe(
            filter((message: IMessage) => {
                return message.type === MessageType.Leaderboard;
            }),
            map((message: LeaderboardMessage) => {
                return message.leaderboard;
            })
        );
    }

    public getPlayersInfo(): Observable<IPlayerInfo[]> {
        return this.leaderboardData$;
    }

    public bindToPlayer(playerId: string): void {
        this.webSocketService.send(new BindPlayerMessage(playerId));
    }

    public unbindToPlayer(): void {
        this.webSocketService.send(new BindPlayerMessage(''));
    }
}



