import { CharacterInfo } from './characterInfo';
import { Output, EventEmitter } from '@angular/core';
import { PlayerAnimation } from '../game/animation/playerAnimation';
import { AnimationDirection } from '../shared/animation/animationDirection';
import { IAction } from '../shared/actions/iAction';
import { Point } from 'pixi.js';

export abstract class ICharacter {
    protected info: CharacterInfo;

    protected currentAnimation: PlayerAnimation;

    protected spritePosition: Point;
    // We should try to get rid of characterPosition since we have the information in this.info.
    protected characterPosition: Point;

    protected positionModifier: Point;
    protected scaleModifier: Point;
    protected totalMapSize: Point;

    @Output()
    public changeAnimation: EventEmitter<any> = new EventEmitter();

    public abstract animate(action: IAction): void;
    public abstract die(point: Point): void;
    protected abstract updateAnimation(): void;
    protected abstract createAnimations(scaleModifier: Point, color: string): void;

    public constructor(info: CharacterInfo, positionModifier: Point, scaleModifier: Point,
        color: string, totalMapSize: Point) {
        this.info = info;
        this.characterPosition = new Point(this.info.position.x, this.info.position.y);
        this.spritePosition = new Point();
        this.update(this.info, positionModifier, scaleModifier, totalMapSize);
        this.createAnimations(scaleModifier, color);
    }

    protected setCurrentAnimation(anim: PlayerAnimation): void {
        this.currentAnimation.endAnimation();
        anim.position = this.currentAnimation.position;
        const oldAnimation = this.currentAnimation;
        this.currentAnimation = anim;
        this.changeAnimation.emit({ oldAnimation: oldAnimation, newAnimation: this.currentAnimation });
    }

    protected calculatePositionOnScreen(): void {
        this.spritePosition.x = (this.totalMapSize.x + ((this.characterPosition.x -
            this.positionModifier.x) % this.totalMapSize.x)) % this.totalMapSize.x;
        this.spritePosition.y = (this.totalMapSize.y + ((this.characterPosition.y -
            this.positionModifier.y) % this.totalMapSize.y)) % this.totalMapSize.y;

        this.spritePosition.x *= this.scaleModifier.x;
        this.spritePosition.y *= this.scaleModifier.y;
    }

    protected getAnimationDirectionFromVector(direction: Point): AnimationDirection {
        if (direction.y === 0 && direction.x === -1) {
            return AnimationDirection.LEFT;
        } else if (direction.y === 0 && direction.x === 1) {
            return AnimationDirection.RIGHT;
        } else if (direction.y === -1 && direction.x === 0) {
            return AnimationDirection.UP;
        } else if (direction.y === 1 && direction.x === 0) {
            return AnimationDirection.DOWN;
        }
        return null;
    }

    protected move(direction: Point): void {
        this.characterPosition.x += direction.x;
        this.characterPosition.y += direction.y;
    }

    protected playAnimation(directionPt: Point): void {
        const direction = this.getAnimationDirectionFromVector(directionPt);
        if (Object.values(AnimationDirection).includes(direction)) {
            this.currentAnimation.execute(direction);
        }
    }

    public update(characterInfo: CharacterInfo, positionModifier: Point,
        scaleModifier: Point, totalMapSize: Point) {

        this.info = characterInfo;

        this.positionModifier = positionModifier;
        this.scaleModifier = scaleModifier;
        this.totalMapSize = totalMapSize;

        this.updateCharacterPosition(this.info.position);
    }

    protected updateCharacterPosition(position: Point): void {
        if (this.currentAnimation) {
            this.currentAnimation.endAnimation();
        }
        this.characterPosition = position;
        this.spritePosition = new Point();
        this.calculatePositionOnScreen();
        this.updateAnimation();
    }

    protected getCurrentPosition(): Point {
        return this.characterPosition;
    }

    public getCurrentAnimation(): PlayerAnimation {
        return this.currentAnimation;
    }

}



