import * as PIXI from 'pixi.js';

import { AnimationDirection } from '../../shared/animation/animationDirection';
import { PlayerAnimation } from './playerAnimation';

const FRAME_RATE = 60;
const NUMBER_DOWN_FRAMES = 11;
const NUMBER_SIDE_FRAMES = 11;
const NUMBER_UP_FRAMES = 11;

export class WalkAnimation extends PlayerAnimation {

    private destinationPoint: PIXI.Point;
    private gridDimension: PIXI.Point;

    public constructor(username: string, position: PIXI.Point, gridDimension: PIXI.Point, color: string) {
        const textures = PlayerAnimation.loadTextures(color, 'walk', NUMBER_DOWN_FRAMES, NUMBER_SIDE_FRAMES, NUMBER_UP_FRAMES);
        super(textures.up, textures.down, textures.side, username, position, gridDimension);
        this.actionName = 'walk';
        this.animation.loop = true;
        this.gridDimension = gridDimension;
    }

    protected translateAnimation(): void {
        const translationPerFrameWidth = this.gridDimension.x / FRAME_RATE;
        const translationPerFrameHeight = this.gridDimension.y / FRAME_RATE;
        let canStop = false;
        switch (this.currentDirection) {
            case AnimationDirection.UP:
                this.y -= translationPerFrameHeight;
                canStop = this.y <= this.destinationPoint.y;
                break;
            case AnimationDirection.DOWN:
                this.y += translationPerFrameHeight;
                canStop = this.y >= this.destinationPoint.y;
                break;
            case AnimationDirection.LEFT:
                this.x -= translationPerFrameWidth;
                canStop = this.x <= this.destinationPoint.x;
                break;
            case AnimationDirection.RIGHT:
                this.x += translationPerFrameWidth;
                canStop = this.x >= this.destinationPoint.x;
                break;
            default:
                this.endAnimation();
                return;
        }
        if (canStop) {
            this.endAnimation();
        }
    }

    public execute(direction: AnimationDirection): void {
        this.setDirection(direction);
        this.destinationPoint = new PIXI.Point(this.x, this.y);
        switch (direction) {
            case AnimationDirection.UP:
                this.destinationPoint.y -= this.gridDimension.y;
                break;
            case AnimationDirection.DOWN:
                this.destinationPoint.y += this.gridDimension.y;
                break;
            case AnimationDirection.LEFT:
                this.destinationPoint.x -= this.gridDimension.x;
                break;
            case AnimationDirection.RIGHT:
                this.destinationPoint.x += this.gridDimension.x;
                break;
            default:
                return;
        }
        PIXI.ticker.shared.add(this.translateAnimation, this);
        this.animation.play();
    }

    public endAnimation(): void {
        PIXI.ticker.shared.remove(this.translateAnimation, this);
        super.endAnimation();
    }

    public update(newPosition: PIXI.Point, gridDimension: PIXI.Point): void {
        super.update(newPosition, gridDimension);
        this.gridDimension = gridDimension;
    }

    public reset(): void {
        this.setDirection(AnimationDirection.DOWN);
    }
}



