import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { environment } from '../../../environments/environment';
import { IPlayerInfo } from '../../leaderboard/player-info';
import { Observer } from 'rxjs/Observer';
import { Subject } from 'rxjs/Subject';
import { IMessage } from '../messages/iMessage';

@Injectable()
export class WebSocketService {
    public readonly message$: Subject<IMessage>;
    private socket: WebSocket;

    constructor() {
        this.socket = new WebSocket(`${environment.WS_URL}`);
        this.message$ = new Subject();

        this.socket.onmessage = (message: MessageEvent) => {
            const messageData = JSON.parse(message.data) as IMessage;
            this.message$.next(messageData);
        };
    }

    public send(message: IMessage) {
        this.socket.send(JSON.stringify(message));
    }
}



